.text-center {
  text-align: center; }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

::-webkit-scrollbar {
  width: 10px; }

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: #244A55; }

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #244A55; }

* {
  box-sizing: border-box; }

*::-moz-selection {
  background-color: var(--gris);
  color: #ffffff; }

*::selection {
  background-color: var(--gris);
  color: #ffffff; }

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "KoHo", sans-serif;
  font-size: 1.6rem; }

p {
  color: #ffffff;
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1, h2, h3 {
  font-family: "KoHo", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #244A55;
  text-align: center; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem;
  text-align: center; }

.header {
  background: linear-gradient(#ffffff, transparent);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center; }
  @media (min-width: 768px) {
    .header {
      position: fixed;
      width: 100%;
      flex-direction: row; } }
  .header .header__logo .logo__header {
    background-image: url("../img/LOGO-02.webp");
    background-size: cover;
    background-position: center center;
    height: 6rem;
    width: 6rem;
    margin-left: .3rem; }
  .header nav {
    width: 100%; }
    @media (min-width: 768px) {
      .header nav {
        width: auto; } }
    .header nav ul {
      display: flex;
      flex-direction: column;
      background-color: #244A55; }
      @media (min-width: 768px) {
        .header nav ul {
          background-color: transparent; } }
      @media (min-width: 768px) {
        .header nav ul {
          flex-direction: row;
          justify-content: space-around;
          column-gap: 2rem;
          flex-wrap: wrap; } }
      .header nav ul li {
        padding: 1rem;
        border-bottom: 0.2rem solid #ffffff;
        text-align: center; }
        @media (min-width: 768px) {
          .header nav ul li {
            padding: 0;
            border: none; } }
        .header nav ul li a {
          width: 100%;
          padding: 1rem 2rem;
          color: #ffffff; }
          @media (min-width: 768px) {
            .header nav ul li a {
              padding: 1rem 1rem;
              color: #244A55; } }
          .header nav ul li a:hover {
            text-decoration: underline; }

.container {
  background-image: url("../img/login.webp");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  min-height: 100vh;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center; }

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  background-color: #244A55; }
  @media (min-width: 768px) {
    .footer {
      flex-direction: row; } }
  .footer .derechos {
    text-align: center;
    margin: 2rem; }
    @media (min-width: 768px) {
      .footer .derechos {
        margin: 1rem 2rem; } }
  .footer p {
    margin: 2rem; }
    @media (min-width: 768px) {
      .footer p {
        margin: 1rem 2rem; } }

.formulario {
  background-color: #000000d7; }
  .formulario h1 {
    color: #ffffff;
    padding-top: 3rem;
    font-weight: 100;
    font-size: 2.9rem; }
  .formulario form {
    display: flex;
    flex-direction: column;
    padding: 0 5rem;
    min-width: 20rem; }
    .formulario form label {
      color: #ffffff;
      font-size: 2rem;
      margin-bottom: 1rem; }
    .formulario form input, .formulario form select {
      font-size: 2.3rem;
      padding: .8rem;
      margin-bottom: 1.7rem;
      background-color: transparent;
      border: none;
      border-bottom: 0.2rem solid #ffffff;
      outline: none;
      color: #ffffff;
      text-align: center; }
    .formulario form p {
      margin: 1rem auto; }
    .formulario form option {
      background-color: #000000d7; }
    .formulario form .btn {
      color: #000000d7;
      background-color: #ffffff81;
      cursor: pointer;
      font-size: 2rem;
      margin-bottom: 3rem;
      max-width: 5rem; }
      .formulario form .btn:hover {
        background-color: #ffffff; }

.mr {
  margin-left: 4rem; }

.tabla {
  max-height: 50rem;
  min-width: 50rem;
  overflow: auto; }
  .tabla h1 {
    margin-top: 1rem; }
  .tabla table {
    width: 100%;
    color: #ffffff;
    text-align: center;
    background-color: #000000d7;
    border-collapse: collapse; }
    .tabla table thead {
      background-color: #244A55;
      text-transform: uppercase; }
    .tabla table th, .tabla table td {
      padding: 1.5rem;
      font-weight: bold; }
    .tabla table tr:nth-child(even) {
      background-color: #000000d7;
      color: #ffffff; }

.tablaActEli input {
  background-color: transparent;
  border: none;
  color: #ffffff;
  max-width: 10rem;
  font-size: 1.8rem; }

.tablaActEli .btnactli,
.tablaActEli .btnelili {
  border: none;
  padding: .5rem .3rem; }
  .tablaActEli .btnactli:hover,
  .tablaActEli .btnelili:hover {
    cursor: pointer; }

.tablaActEli .btnactli {
  background-color: #3cbd00; }

.tablaActEli .btnelili {
  background-color: #ac0101f6; }
